
.header {
    display: flex;
    justify-content: center;
    font-size: 14px;
    height: 7vh;
    /*width: 100%;*/
    z-index: 22;
    position: relative;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    scroll-behavior: smooth
}

.main {
    position: fixed;
    min-height: 55px;
    width: 100%;
    /*border-bottom: 5px solid;*/
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 13px #cccccc;
    /*border-color: rgba(55, 55, 55, 0.78);*/
    background-color: #000000;
}
